.modal-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.modal-overlay
.modal-content-container {
  width: 90%;
  text-align: center;
}

.modal-overlay
.modal-content-container
img {
  max-width: 100%;
  max-height: 100vh;
}

.modal-overlay
.modal-button {
  border-radius: 100%;
  width: 50px;
  height: 50px;
  /* box-shadow: 0 5px 20px rgba(0, 0, 0, 0.26); */
}

.modal-overlay
.modal-content-container
#modal-close-button {
  position: absolute;
  top: 50%;
  right: 1%;

  -webkit-transform: translateY(-50%);

      -ms-transform: translateY(-50%);

          transform: translateY(-50%);
}

.modal-overlay
.modal-content-container
#modal-next-button {
  position: absolute;
  top: 50%;
  right: 1%;

  -webkit-transform: translateY(-100px);

      -ms-transform: translateY(-100px);

          transform: translateY(-100px);
}

.modal-overlay
.modal-content-container
#modal-previous-button {
  position: absolute;
  bottom: 50%;
  right: 1%;

  -webkit-transform: translateY(100px);

      -ms-transform: translateY(100px);

          transform: translateY(100px);
}

@media only screen and (min-width: 1000px) {
  .modal-overlay
  .modal-button {
    width: 100px;
    height: 100px;
  }

  .modal-overlay
  .modal-content-container
  #modal-close-button {
    left: 1%;
  }

  .modal-overlay
  .modal-content-container
  #modal-next-button {
    position: absolute;
    top: 50%;
    right: 1%;

    -webkit-transform: translateY(-200px);

        -ms-transform: translateY(-200px);

            transform: translateY(-200px);
  }

  .modal-overlay
  .modal-content-container
  #modal-previous-button {
    position: absolute;
    bottom: 50%;
    right: 1%;

    -webkit-transform: translateY(200px);

        -ms-transform: translateY(200px);

            transform: translateY(200px);
  }
}

.animate-start-next {
  -webkit-transform: translateX(50px);
      -ms-transform: translateX(50px);
          transform: translateX(50px);
  opacity: 0;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.animate-start-previous {
  -webkit-transform: translateX(-50px);
      -ms-transform: translateX(-50px);
          transform: translateX(-50px);
  opacity: 0;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.animate-end {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}



