.responsive-image {
  width: 100%;
  max-width: 100%;

  text-align: center;

  -webkit-transition: 1s;

  -o-transition: 1s;

  transition: 1s;
  opacity: 0;
}

.responsive-image.is-clickable:hover {
  cursor: pointer;
}

.responsive-image.fadeIn {
  opacity: 1;
}

.responsive-image
img {
  max-width: 100%;
}



