.link {
  text-decoration: none;
  display: inline-block;
  color: #aeb0b1;

  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.link:hover {
  cursor: pointer;
  color: #ffd6f4;
}

.link-container {
  display: inline-block;
}



