.title-thing .stuff {
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
  position: relative;
  display: inline-block;
  margin-right: 4px;
}

.animate {
  -webkit-transform: translateY(-50px);
      -ms-transform: translateY(-50px);
          transform: translateY(-50px);
  opacity: 1;
}

.still {
  opacity: 0;
}



